#bg{
    background: #3498db; /* For browsers that do not support gradients */
    background: -webkit-gradient(linear, left top, right bottom, from(#8e44ad), color-stop(#2980b9),to(#9b59b6));
    background: -webkit-linear-gradient(top left, #8e44ad, #2980b9,#9b59b6);
    background: -o-linear-gradient(top left, #8e44ad, #2980b9,#9b59b6);
    background: linear-gradient(to bottom right, #8e44ad, #2980b9,#9b59b6); /* Standard syntax (must be last) */
}
